<template>
  <div class="gradient-canvas-container">
    <div 
      class="gradient-canvas" 
      :style="{backgroundImage: gradientCss}"
      @mousedown="startDrag"
      @touchstart="startDrag"
    >
      <!--    -->
      <div 
        v-for="color in sortedColors" 
        :key="color.id"
        class="color-marker"
        :class="{ 'fixed': color.fixed, 'active': activeMarkerId === color.id }"
        :style="markerStyle(color)"
        @mousedown.stop="selectMarker($event, color.id)"
        @touchstart.stop="selectMarker($event, color.id)"
      >
        <div 
          class="marker-handle"
          :style="{ backgroundColor: color.color }"
        ></div>
      </div>
    </div>
    
    <!--      -->
    <div class="gradient-info">
      <div class="gradient-type">
        {{ typeLabel }}: {{ colorCount }}
      </div>
      <div v-if="gradientType === 'linear'" class="gradient-direction">
        {{ directionLabel }}
      </div>
    </div>
    
    <!--    -->
    <div class="color-info-container">
      <div 
        v-for="color in sortedColors" 
        :key="`info-${color.id}`"
        class="color-info"
        :class="{ 'fixed': color.fixed }"
      >
        <div 
          class="color-swatch" 
          :style="{ backgroundColor: color.color }"
        ></div>
        <div class="color-code">{{ color.color }}</div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed, onMounted, onBeforeUnmount, watch } from 'vue';
import { ColorInfo } from '@/types';

export default defineComponent({
  name: 'GradientCanvas',
  
  props: {
    gradientCss: {
      type: String,
      default: 'linear-gradient(to right, #000000 0%, #ffffff 100%)'
    },
    colors: {
      type: Array as () => ColorInfo[],
      required: true,
    },
    gradientType: {
      type: String,
      default: 'linear',
    },
    gradientDirection: {
      type: String,
      default: 'to right',
    },
  },
  
  emits: ['update:color-position'],
  
  setup(props, { emit }) {
    const canvasRef = ref<HTMLElement | null>(null);
    const activeMarkerId = ref<string | null>(null);
    const isDragging = ref(false);
    
    //  
    const colorCount = computed(() => props.colors.length);
    
    //   
    const typeLabel = computed(() => {
      switch(props.gradientType) {
        case 'linear': return ' ';
        case 'radial': return ' ';
        case 'conic': return ' ';
        default: return '';
      }
    });
    
    //  
    const directionLabel = computed(() => {
      const directionMap: Record<string, string> = {
        'to right': ' ',
        'to left': ' ',
        'to top': ' ',
        'to bottom': ' ',
        'to top right': '  ',
        'to top left': '  ',
        'to bottom right': '  ',
        'to bottom left': '  ',
      };
      
      return directionMap[props.gradientDirection] || props.gradientDirection;
    });
    
    //     
    const sortedColors = computed(() => {
      return [...props.colors].sort((a, b) => a.position - b.position);
    });
    
    //   
    const markerStyle = (color: ColorInfo) => {
      return {
        left: `${color.position}%`,
      };
    };
    
    //  
    const selectMarker = (event: MouseEvent | TouchEvent, id: string) => {
      event.preventDefault();
      activeMarkerId.value = id;
      isDragging.value = true;
      
      // /   
      document.addEventListener('mousemove', handleDrag);
      document.addEventListener('touchmove', handleDrag);
      document.addEventListener('mouseup', stopDrag);
      document.addEventListener('touchend', stopDrag);
    };
    
    //    
    const startDrag = (event: MouseEvent | TouchEvent) => {
      if (!(event.target as HTMLElement).classList.contains('gradient-canvas')) {
        return;
      }
      
      const canvas = event.currentTarget as HTMLElement;
      const rect = canvas.getBoundingClientRect();
      
      // /  
      let clientX: number;
      
      if ('touches' in event) {
        clientX = event.touches[0].clientX;
      } else {
        clientX = event.clientX;
      }
      
      const offsetX = clientX - rect.left;
      const positionPercent = (offsetX / rect.width) * 100;
      
      //    
      let closestMarker: ColorInfo | null = null;
      let minDistance = Infinity;
      
      for (const color of props.colors) {
        const distance = Math.abs(color.position - positionPercent);
        if (distance < minDistance) {
          minDistance = distance;
          closestMarker = color;
        }
      }
      
      //    
      if (closestMarker) {
        activeMarkerId.value = closestMarker.id;
        isDragging.value = true;
        
        //  
        updateMarkerPosition(positionPercent);
        
        // /   
        document.addEventListener('mousemove', handleDrag);
        document.addEventListener('touchmove', handleDrag);
        document.addEventListener('mouseup', stopDrag);
        document.addEventListener('touchend', stopDrag);
      }
    };
    
    //   
    const handleDrag = (event: MouseEvent | TouchEvent) => {
      if (!isDragging.value || !activeMarkerId.value) return;
      
      const canvas = event.currentTarget as Document;
      const canvasEl = document.querySelector('.gradient-canvas') as HTMLElement;
      
      if (!canvasEl) return;
      
      const rect = canvasEl.getBoundingClientRect();
      
      // /  
      let clientX: number;
      
      if ('touches' in event) {
        clientX = event.touches[0].clientX;
      } else {
        clientX = event.clientX;
      }
      
      const offsetX = clientX - rect.left;
      const positionPercent = Math.max(0, Math.min(100, (offsetX / rect.width) * 100));
      
      //  
      updateMarkerPosition(positionPercent);
    };
    
    //   
    const updateMarkerPosition = (positionPercent: number) => {
      if (!activeMarkerId.value) return;
      
      //  
      emit('update:color-position', {
        id: activeMarkerId.value,
        position: positionPercent
      });
    };
    
    //  
    const stopDrag = () => {
      isDragging.value = false;
      activeMarkerId.value = null;
      
      //   
      document.removeEventListener('mousemove', handleDrag);
      document.removeEventListener('touchmove', handleDrag);
      document.removeEventListener('mouseup', stopDrag);
      document.removeEventListener('touchend', stopDrag);
    };
    
    //     
    onMounted(() => {
      canvasRef.value = document.querySelector('.gradient-canvas');
    });
    
    //      
    onBeforeUnmount(() => {
      document.removeEventListener('mousemove', handleDrag);
      document.removeEventListener('touchmove', handleDrag);
      document.removeEventListener('mouseup', stopDrag);
      document.removeEventListener('touchend', stopDrag);
    });
    
    return {
      canvasRef,
      sortedColors,
      colorCount,
      activeMarkerId,
      typeLabel,
      directionLabel,
      markerStyle,
      selectMarker,
      startDrag,
    };
  },
});
</script>

<style scoped>
.gradient-canvas-container {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  position: relative;
}

.gradient-canvas {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  user-select: none;
}

.color-marker {
  position: absolute;
  bottom: 15px;
  transform: translateX(-50%);
  width: 20px;
  height: 36px;
  cursor: move;
  z-index: 10;
}

.marker-handle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease;
}

.color-marker::after {
  content: '';
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 16px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.color-marker.fixed .marker-handle {
  border: 2px solid #ff5722;
}

.color-marker.active .marker-handle {
  transform: scale(1.2);
  border: 2px solid #2196f3;
}

.gradient-info {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(0, 0, 0, 0.5);
  padding: 8px 12px;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.color-info-container {
  position: absolute;
  bottom: 60px;
  left: 15px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.color-info {
  display: flex;
  align-items: center;
  gap: 6px;
  background: rgba(0, 0, 0, 0.5);
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  color: white;
}

.color-info.fixed {
  border-left: 2px solid #ff5722;
}

.color-swatch {
  width: 12px;
  height: 12px;
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.color-code {
  font-family: 'Roboto Mono', monospace;
}
</style>
